<template lang="html">
  <div class="script-title">
    <div class="">
      <div class="script">
        <div class="script__left">
          <HelpList :getHelp="getHelp" />
        </div>
        <div class="script__right">
          <BookingSetting :getHelp="getHelp" v-if="whichHelpListCLicked == 'Booking Setting'" />
          <DaysOff :getHelp="getHelp" v-if="whichHelpListCLicked == 'Days Off'" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import HelpList from '../../components/help-page/HelpList'
import DaysOff from '../../components/help-page/help-descriptions/DaysOff'
import BookingSetting from '../../components/help-page/help-descriptions/BookingSetting'

export default {
  components: {
    HelpList,
    DaysOff,
    BookingSetting
  },
  created () {
    this.$store.dispatch('GET_HELP');
  },
  computed: {
    getHelp () {
      const data = this.$store.state.stateHelp

      if (data == null) {
        return 0
      } else {
        return data
      }
    },
    whichHelpListCLicked () {
      const data = this.$store.state.stateClickedHelpList

      if (data == null) {
        return "Booking Setting"
      } else {
        return data
      }
    }
  }
}
</script>

<style lang="scss">
  @import './Help.scss';
  @import '../coaching-script/coachingScript.scss';
</style>



